#mkdf-back-to-top {
  position: fixed;
  bottom: 25px;
  right: 25px;
  margin: 0;
  z-index: 10000;
  opacity: 0;
  visibility: hidden;
  color: #d9d3cc;
}

#mkdf-back-to-top.off {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.15s ease, visibility .0s .15s;
}

#mkdf-back-to-top.on {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease;
}

#mkdf-back-to-top svg.mkdf-back-to-top {
  width: 57px;
  height: 53px;
}

#mkdf-back-to-top svg.mkdf-back-to-top .mkdf-back-to-top-circle {
  fill: currentColor;
  transition: .3s;
}

#mkdf-back-to-top svg.mkdf-back-to-top .mkdf-back-to-top-arrow {
  fill: #fff;
}

#mkdf-back-to-top svg.mkdf-bark-1 {
  width: 28.2px;
  height: 21.8px;
  position: absolute;
  top: -20px;
  left: -2px;
  transform: scale(0);
}

#mkdf-back-to-top svg.mkdf-bark-1 path {
  fill: currentColor;
}

#mkdf-back-to-top svg.mkdf-bark-2 {
  width: 24.1px;
  height: 12.1px;
  position: absolute;
  top: -12px;
  right: -2px;
  transform: scale(0);
}

#mkdf-back-to-top svg.mkdf-bark-2 path {
  fill: currentColor;
}

#mkdf-back-to-top:hover {
  color: #cb2f23;
}

#mkdf-back-to-top:hover svg.mkdf-bark-1,
#mkdf-back-to-top:hover svg.mkdf-bark-2 {
  transform: scale(1);
  transition: 0.1s ease-out;
}

#mkdf-back-to-top:hover svg.mkdf-bark-2 {
  transition-delay: 0.1s;
}

@media only screen and (max-width: 480px) {
  #mkdf-back-to-top svg.mkdf-back-to-top {
    width: 46px;
    height: 46px;
  }
}

