@media only screen and (max-width: 768px) {
  .mkdf-cf7-contact-form-newsletter {
    display: block;
  }
  .mkdf-cf7-contact-form-newsletter .mkdf-cf7-col-long,
  .mkdf-cf7-contact-form-newsletter .mkdf-cf7-col-short {
    display: block;
    width: 100%;
    text-align: center;
  }
  .mkdf-cf7-contact-form-newsletter .mkdf-cf7-col-long button.wpcf7-form-control.wpcf7-submit.mkdf-btn,
  .mkdf-cf7-contact-form-newsletter .mkdf-cf7-col-short button.wpcf7-form-control.wpcf7-submit.mkdf-btn {
    margin: 0;
  }
  .mkdf-cf7-contact-form-newsletter .mkdf-cf7-col-long .ajax-loader,
  .mkdf-cf7-contact-form-newsletter .mkdf-cf7-col-short .ajax-loader {
    display: block;
  }
}

.mkdf-cf7-contact-form-newsletter span.wpcf7-not-valid-tip {
  color: #fff;
}

.mkdf-search-submit:hover svg {
  animation: mkdf-wiggle-icon-animation .5s forwards;
}

@keyframes mkdf-animate-solid-button-bg {
  0%, 100% {
    background-position: top right;
  }
  50% {
    background-position: 96% -5px;
  }
}

.wpcf7-submit.mkdf-btn.mkdf-btn-solid:hover, .add_to_cart:hover, .added_to_cart:hover, .add_to_cart_button:hover, .ajax_add_to_cart:hover, .button.product_type_simple:hover, .single_add_to_cart_button:hover, .mkdf-paws.woocommerce-page .mkdf-content input[type=submit]:hover, .woocommerce-form-login__submit:hover, .actions button:hover, .checkout-button:hover, .button.wc-backward:hover {
  animation: mkdf-animate-solid-button-bg .7s;
}

.add_to_wishlist:after, .yith-wcwl-wishlistaddedbrowse a:after, .yith-wcwl-wishlistexistsbrowse a:after, .mkdf-pl-holder.mkdf-info-on-image .mkdf-pli a.button.add_to_cart_button:after {
  display: inline-block;
}

.add_to_wishlist:hover:after, .yith-wcwl-wishlistaddedbrowse a:hover:after, .yith-wcwl-wishlistexistsbrowse a:hover:after, .mkdf-pl-holder.mkdf-info-on-image .mkdf-pli a.button.add_to_cart_button:hover:after {
  animation: mkdf-animate-simple-button .6s forwards;
}

.yith-wcwl-wishlistaddedbrowse a:hover:after {
  animation: none !important;
}

