/* ==========================================================================
   4. Widgets styles
   ========================================================================== */
s aside.mkdf-sidebar {
  position: relative;
  z-index: 11;
  margin: 0 0 60px;
}

.wpb_widgetised_column {
  position: relative;
}

/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */
/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */
/*
* $title-adjusted-size variable is default body ( line-height - font-size ) / 2 + 3 where 3 is temporary value, it's half size of widget title line height minus font size,
* $wp-widgets-map input-fields-... variables are for select and for search elements
*/
.widget {
  margin: 0 0 61px;
}

.widget:last-child {
  margin-bottom: 0 !important;
}

.widget .mkdf-widget-title {
  margin: 0 0 21px;
}

.widget .mkdf-widget-title a {
  color: inherit;
}

.widget .mkdf-widget-title a:hover {
  color: #f04336;
}

.widget a {
  color: #8f9093;
}

.widget a:hover {
  color: #f04336;
}

.widget ul:not(.mkdf-shortcode-list):not(.mkdf-instagram-gallery) {
  padding: 0;
  margin: 0;
  list-style: none;
}

.widget ul:not(.mkdf-shortcode-list):not(.mkdf-instagram-gallery) li {
  margin: 0 0 18px;
}

.widget ul:not(.mkdf-shortcode-list):not(.mkdf-instagram-gallery) li:last-child {
  margin-bottom: 0;
}

.widget ul:not(.mkdf-shortcode-list):not(.mkdf-instagram-gallery) li ul {
  margin-top: 18px;
  margin-left: 18px;
}

.widget.widget_archive select, .widget.widget_categories select, .widget.widget_text select {
  width: 100%;
  max-width: 100%;
  height: 60px;
  margin: 10px 0 0;
  padding: 18px 31px;
  font-family: inherit;
  font-size: 15px;
  line-height: 24px;
  font-style: inherit;
  font-weight: inherit;
  color: #424242;
  background-color: #f7f7f5;
  border: 1px solid transparent;
  border-radius: 0;
  outline: none;
  box-sizing: border-box;
}

.widget.widget_archive ul li {
  color: #424242;
}

.widget.widget_calendar {
  width: 100%;
  color: #424242;
}

.widget.widget_calendar #today {
  color: #fff;
  background-color: #f04336;
}

.widget.widget_calendar #today a {
  color: #fff;
}

.widget.widget_calendar tfoot a {
  color: #f04336;
}

.widget.widget_calendar tfoot a:hover {
  color: #f04336;
}

.widget.widget_calendar caption {
  margin-bottom: 3px;
}

.widget.widget_calendar a {
  color: #f04336;
}

.widget.widget_text img {
  display: block;
}

.widget.widget_rss ul li {
  margin-bottom: 30px;
}

.widget.widget_rss ul li:last-child {
  margin-bottom: 0;
}

.widget.widget_rss ul .rsswidget {
  display: block;
  font-size: 16px;
  line-height: 1.188em;
  color: #4a4a4a;
}

.widget.widget_rss ul .rssSummary {
  margin-top: 5px;
}

.widget.widget_rss ul cite {
  display: block;
  margin-top: 5px;
}

.widget.widget_search .mkdf-widget-title, .widget.widget_product_search .mkdf-widget-title {
  margin-bottom: 31px;
}

.widget.widget_search .input-holder, .widget.widget_product_search .input-holder {
  position: relative;
  display: inline-block;
  width: 100%;
  vertical-align: middle;
}

.widget.widget_search input,
.widget.widget_search button, .widget.widget_product_search input,
.widget.widget_product_search button {
  height: 60px;
  padding: 18px 0;
  font-family: inherit;
  font-weight: inherit;
  line-height: 24px;
  border-radius: 4px;
  outline: 0;
  -webkit-appearance: none;
  box-sizing: border-box;
