@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    margin-bottom: 58px;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 20px 0 20px;
    }
    
    .mkdf-comments-title {
        margin-bottom: 34px;
    }

    .mkdf-comment {
        position: relative;

        &.mkdf-pingback-comment {

            .mkdf-comment-text {
                padding: 0;
            }
        }

        svg.mkdf-reply-comment {
            position: absolute;
            top: -19px;
            left: -54px;

            path {
                fill: $first-main-color;
            }
        }
    }

    .mkdf-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include mkdfRelativeHolderLayout();
            margin: 11px 0 0;
            padding: 29px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 110px;
        }
    }

    .mkdf-comment-image {
        position: relative;
        top: -11px;
        left: 0;
        width: 153px;
        height: 131px;
        display: block;
        float: left;

        img {
            display: none;
        }
    }

    .mkdf-comment-text {
        padding: 0 0 0 182px;
        min-height: 82px;

        .mkdf-comment-info {
            @include mkdfRelativeHolderLayout();
            margin: 6px 0;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        .comment-reply-link {

            svg {

                path {
                    fill: $first-main-color;
                }
            }
        }

        .mkdf-comment-name {
            @include mkdfH5;
            float: left;
            margin: 0 13px 0 0;
        }

        .mkdf-comment-date {
            @include mkdfDate();
            color: $default-heading-color;
            float: left;
            margin: 0;
        }

        .mkdf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

@include ipad-landscape {

    .mkdf-comment-holder {

        .mkdf-comment-image {

            svg {
                display: none;
            }

            img {
                display: block;
            }
        }
    }
}

.mkdf-comment-form {
    @include mkdfRelativeHolderLayout();
    margin: 0 0 60px;
    clear: both;

    .comment-reply-title {
        margin: 0 0 25px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }

    p.comment-form-cookies-consent {
        margin: 15px 0 27px;

        input#wp-comment-cookies-consent {
            position: relative;
            top: 2px;
            margin-right: 5px;
        }
    }
}

@include ipad-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0;
            }
        }
    }
}

@include phone-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            padding: 0;
            min-height: inherit;

            .mkdf-comment-name {
                float: none;
                margin-bottom: 3px;
            }

            .mkdf-comment-date {
                font-size: 20px;
            }

            .mkdf-comment-info {
                margin: 6px 0 0;
            }
        }

        svg.mkdf-reply-comment {
            display: none;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;

    &:focus {
        border-color: transparent;
    }
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit']{
    @include mkdfButtonDefaultStyle();
    @include mkdfButtonSize();
    @include mkdfButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include mkdfButtonSolidHoverColor();
    }
}

#respond input[type="text"]:last-of-type:not(:last-child) {
    margin-bottom: 0;
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
    display: block;
}

.mkdf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
        border-radius: 6px;
    }

    &.mkdf-slider-is-moving {
        .owl-item {
            .mkdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.mkdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                transform: translateY($pos);
            }
        }
    }

    &.mkdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;

            transform: translateY(-50%);
            transition: color .2s ease-out;

            &:hover {

                svg.mkdf-owl-nav {

                    path.mkdf-nav-circle {
                        fill: #cb2f23;
                    }
                }
            }

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 46px;
                line-height: 1;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }

            svg.mkdf-owl-nav {
                overflow: hidden;

                path.mkdf-nav-circle {
                    transition: all .2s ease-out;
                    fill: #d9d3cc;
                }

                path.mkdf-nav-arrow {
                    fill: #fff;
                    stroke: #fff;
                    stroke-width:0.5;
                    stroke-miterlimit:10;
                    transition: .3s;
                }
            }
        }

        .owl-prev {
            left: 25px;

            svg.mkdf-owl-nav {
                    
                path.mkdf-nav-arrow {
                    transform: translateX(6px);
                }
            }

            &:hover {

                svg.mkdf-owl-nav {
                    
                    path.mkdf-nav-arrow {
                        transform: translateX(-7px);
                    }
                }
            }
        }

        .owl-next {
            right: 25px;

            svg.mkdf-owl-nav {
                    
                path.mkdf-nav-arrow {
                    transform: translateX(-6px);
                }
            }

            &:hover {

                svg.mkdf-owl-nav {
                    
                    path.mkdf-nav-arrow {
                        transform: translateX(7px);
                    }
                }
            }
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 9px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 10px;
                height: 10px;
                border-radius: 44% 56% 48% 52% / 39% 39% 61% 61%;
                background-color: #d9d3cc;
                border: 1px solid #d9d3cc;
                box-sizing: border-box;

                transition: background-color .2s ease-out, border .2s ease-out;
            }

            &:hover,
            &.active {

                span {
                    transform: scale(1.3);
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                transition: border-color 0.2s ease-out;
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

@include phone-landscape {

    .mkdf-owl-slider {

        .owl-nav {

            .owl-prev,
            .owl-next {

                svg.mkdf-owl-nav {
                    width: 50px;
                }
            }

            .owl-prev {
                left: 15px;
            }

            .owl-next {
                right: 15px;
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .mkdf-wrapper {
        padding: 0 !important;
    }

    .mkdf-content {
        height: 100vh;
    }

    &.admin-bar {

        .mkdf-content {
            height: calc(100vh - 32px);
        }
    }

    .mkdf-content-inner {
        @include mkdfTableLayout();
    }

    .mkdf-page-not-found {
        @include mkdfTableCellLayout();
        padding: 0 20%;
        text-align: center;

        .mkdf-404-title {
            margin: 0;
            font-family: Amatic SC;
            font-size: 196px;
            line-height: 178px;
            font-weight: 400;
            color: $first-main-color;

            .mkdf-404-highlight-title-holder {
                @include mkdfSVGbg(#fff);
                position: relative;
                display: inline-block;

                .mkdf-404-highlight-title {
                    display: block;
                    padding: 0 30px;
                    position: relative;
                    color: $first-main-color;
                    z-index: 1;
                }
            }
        }

        .mkdf-404-subtitle {
            margin: 24px 0 0;
        }

        .mkdf-404-text {
            margin: 10px 0 28px;
            padding: 0 33%;
            font-family: $default-text-font;
            font-size: 19px;
            color: #fff;
        }

        .mkdf-404-search {
            margin: 0 auto;
            position: relative;
            display: block;
            width: 302px;

            input,
            button {
                height: 60px;
                padding: 18px 0;
                font-family: inherit;
                font-weight: inherit;
                line-height: 24px;
                border-radius: 4px;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
            }

            input {
                @include mkdfRelativeHolderLayout();
                padding-left: 31px;
                padding-right: 60px;
                font-size: 14px;
                color: $default-heading-color;
                background-color: $additional-background-color;
                border: 1px solid transparent;
                transition: border-color .2s ease-out;

                &:focus {
                    color: $default-text-color;
                    background-color: $additional-background-color;
                    border-color: transparent;
                }

                @include mkdfPlaceholder {
                    color: inherit;
                }
            }

            button {
                position: absolute;
                top: 0;
                right: 0;
                width: 60px;
                text-align: center;
                color: #000;
                background-color: $first-main-color;
                border: 0;
                cursor: pointer;
                transition: color .2s ease-out;

                span {
                    display: block;
                    line-height: inherit;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }

                &:hover {
                    color: #000;
                }

                svg.search {
                    position: relative;
                    bottom: 2px;

                    path {
                        fill: none;
                        stroke: #fff;
                        stroke-miterlimit: 10;
                    }
                }
            }
        }

        .mkdf-btn {
            margin: 30px 0 0;

            &.mkdf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include laptop-landscape-large {

        .mkdf-content {

            .mkdf-page-not-found {

                .mkdf-404-text {
                    padding: 0 28%
                }
            }
        }
    }

    @include laptop-landscape-mac {

        .mkdf-content {

            .mkdf-page-not-found {

                .mkdf-404-text {
                    padding: 0 24%
                }
            }
        }
    }

    @include ipad-landscape {

        .mkdf-content {
            height: 100%;
            min-height: 100vh;

            .mkdf-page-not-found {
                display: block;
                padding: 26% 20% 13.7%;
                box-sizing: border-box;

                .mkdf-404-text {
                    padding: 0 20%;
                }
            }
        }

        &.admin-bar {

            .mkdf-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }

    @include ipad-portrait {

        .mkdf-content {

            .mkdf-page-not-found {
                padding: 47% 20% 36%;
            }
        }
    }

    @include phone-portrait {

        .mkdf-content {
            margin-top: -160px !important;

            .mkdf-page-not-found {
                padding: 64% 20% 0%;

                .mkdf-404-search {
                    width: 100%;
                }

                .mkdf-404-title {
                    font-size: 85px;
                    line-height: 85px;
                }

                .mkdf-404-text {
                    padding: 0;
                }

                .mkdf-btn {
                    width: 100%;
                }
            }
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    position: fixed;
    bottom: 25px;
    right: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;
    color: #d9d3cc;

    &.off {
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.15s ease, visibility .0s .15s;
    }

    &.on {
        opacity: 1;
        visibility: visible;
        transition: opacity 0.3s ease;
    }

    svg.mkdf-back-to-top {
        width: 57px;
        height: 53px;

        .mkdf-back-to-top-circle {
            fill: currentColor;
            transition: .3s;
        }

        .mkdf-back-to-top-arrow {
            fill: #fff;
        }
    }

    svg.mkdf-bark-1 {
        width: 28.2px;
        height: 21.8px;
        position: absolute;
        top: -20px;
        left: -2px;
        transform: scale(0);

        path {
            fill: currentColor;
        }
    }

    svg.mkdf-bark-2 {
        width: 24.1px;
        height: 12.1px;
        position: absolute;
        top: -12px;
        right: -2px;
        transform: scale(0);

        path {
            fill: currentColor;
        }
    }

    &:hover {
        color: #cb2f23;

        svg.mkdf-bark-1,
        svg.mkdf-bark-2 {
            transform: scale(1);
            transition: 0.1s ease-out;
        }

        svg.mkdf-bark-2 {
            transition-delay: 0.1s;
        }
    }

    @include phone-portrait {

        svg.mkdf-back-to-top {
            width: 46px;
            height: 46px;
        }
    }
}

/* ==========================================================================
#Back to Top styles - end
========================================================================== */

/* ==========================================================================
#Self Hosted Video styles - begin
========================================================================== */
.mkdf-self-hosted-video-holder {
border-radius: 6px;

.mkdf-video-wrap {
    .mejs-poster,
    .mejs-container,
    .mejs-overlay-play {
        height: 100% !important; /*!important because of the inline style*/
        width: 100% !important; /*!important because of the inline style*/
    }

    .mejs-overlay-button {
        background: url('/wp-content/themes/pawfriends/assets/img/video_play_button.svg') no-repeat;
        height: 61px;
        width: 64px;

        &:after {
            content: '';
            display: inline-block;
            position: relative;
            left: 20px;
            top: 14px;
            border-left: solid 30px $first-main-color;
            border-bottom: solid 15px transparent;
            border-top: solid 15px transparent;
        }

        &[aria-pressed="true"] {

                &:after {
                    display: none;
                }
            }
        }

        .mejs-overlay {

            &:hover {

                >.mejs-overlay-button {
                    background-position: initial;
                }
            }
        }
    }
}

