/* ==========================================================================
   #Custom Contact Form 7 - start
   ========================================================================== */
.wpcf7-form {

    div.wpcf7-validation-errors {
        margin: 2em 0 1em;
        border: 2px solid #fff;
        color: #fff;
    }
}

.mkdf-cf7-contact-form-solid,
.mkdf-cf7-contact-form-simple {

    input[type=email],
    textarea.wpcf7-textarea {
        font-size: 16px;
        padding: 17px 25px 16px;
        background-color: #fff;
        color: #a9a9a9;

        @include mkdfPlaceholder {
            color: #a9a9a9;
        }

        &:focus {
            background-color: #fff;
            border-color: transparent;
        }
    }

    input[type=email] {
        margin: 0 0 19px;
    }

    textarea.wpcf7-textarea {
        margin: 0 0 31px;
        height: 138px;
        resize: none;
    }

    br {
        display: none;
    }

    button.wpcf7-form-control.wpcf7-submit.mkdf-btn.mkdf-btn-solid {
        padding: 15px 26px 13px;
        background-color: #fff;
