    @include ipad-portrait {
        display: block;

        .mkdf-cf7-col-long,
        .mkdf-cf7-col-short {
            display: block;
            width: 100%;
            text-align: center;

            button.wpcf7-form-control.wpcf7-submit.mkdf-btn {
                margin: 0;
            }

            .ajax-loader {
                display: block;
            }
        }
