.mkdf-search-submit {

    &:hover {
        svg {
            animation: mkdf-wiggle-icon-animation .5s forwards;
        }
    }
}

@keyframes mkdf-animate-solid-button-bg {
    0%, 100% {
        background-position: top right;
    }
    50% {
        background-position: 96% -5px;
    }
}

.wpcf7-submit.mkdf-btn.mkdf-btn-solid, .add_to_cart, .added_to_cart, .add_to_cart_button, .ajax_add_to_cart, .button.product_type_simple, .single_add_to_cart_button, .mkdf-paws.woocommerce-page .mkdf-content input[type=submit], .woocommerce-form-login__submit, .actions button, .checkout-button, .button.wc-backward {

    &:hover {
        animation: mkdf-animate-solid-button-bg .7s;
    }
}


.add_to_wishlist, .yith-wcwl-wishlistaddedbrowse a, .yith-wcwl-wishlistexistsbrowse a, .mkdf-pl-holder.mkdf-info-on-image .mkdf-pli a.button.add_to_cart_button {

    &:after {
        display: inline-block;
    }
    
    &:hover {

        &:after {
            animation: mkdf-animate-simple-button .6s forwards;
        }
    }
}

.yith-wcwl-wishlistaddedbrowse a {

    &:hover {
			
        &:after {
            animation: none !important;
        }
    }
}
